@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
html {
	font-size: 62.5%;
}

body {
	font-family: "Nunito Sans", sans-serif;
	font-size: 1.4rem;
}

body {
	/* background-color: #fafafa; */
}

.country-card{box-shadow:0 0.4rem 0.8rem 0 rgba(0,0,0,0.2);transition:0.3s;border-radius:0.5rem;background:#fff;overflow:hidden;display:flex;flex-direction:column;cursor:pointer;width:100%;margin-bottom:40px}.country-card-logo img{object-fit:cover;width:100%;height:20rem}.country-card:hover{box-shadow:0 0.8rem 1.6rem 0 rgba(0,0,0,0.2)}.country-card .container{padding:2.2rem 2rem}.country-card .container .name{font-size:2rem}

.directory-menu-list{display:grid;grid-template-columns:repeat(auto-fill, 280px);align-content:start;grid-gap:20px;gap:20px;justify-content:space-evenly;margin:0 40px}.directory-menu-list .country-list__link{text-decoration:none;color:#000}.directory-menu-filter{display:flex;justify-content:space-between;flex-wrap:wrap;margin:0 50px;margin-bottom:50px}@media (max-width: 530px){.directory-menu-filter{flex-direction:column;align-items:center;margin:0;margin-bottom:40px;margin-top:-10px}}.directory-menu-filter-all{flex:0.4 1;padding:15px;height:52px;border-radius:5px;box-shadow:0 0.4rem 0.8rem 0 rgba(0,0,0,0.1);border:none;margin-left:25px}@media (max-width: 530px){.directory-menu-filter-all{flex:1 1;display:block;margin:0 auto;margin-bottom:50px;height:50px;width:90%}}.directory-menu-filter-region{margin-right:25px}@media (max-width: 530px){.directory-menu-filter-region{align-self:flex-start;margin:0 auto;margin-left:20px;margin-top:-5px}}.ui.selection.dropdown{padding:10px;height:45px;border-radius:5px;box-shadow:0 0.4rem 0.8rem 0 rgba(0,0,0,0.1);border:none;display:flex;align-items:center}.ui.selection.dropdown .divider.default.text{color:#000}.ui.active.visible.selection.dropdown .visible.menu.transition>*{font-size:16px;padding:10px !important;padding-right:20px}.ui.selection.dropdown .menu{max-height:-webkit-min-content;max-height:-moz-min-content;max-height:min-content}.loading{display:flex;justify-content:center;align-items:center;height:70vh}


.header{display:flex;justify-content:space-between;height:70px;background:#fff;align-items:center;box-shadow:0 0.3rem 0.8rem 0 rgba(0,0,0,0.1);position:-webkit-sticky;position:sticky;z-index:1000;top:0;margin-bottom:35px}@media (max-width: 530px){.header{height:90px;padding:0 20px}}.header__title{font-size:20px;font-weight:700;margin-left:80px}@media (max-width: 530px){.header__title{margin:0;font-size:15px}}.header__toggle{margin-right:80px;font-weight:600;text-transform:capitalize;cursor:pointer}@media (max-width: 420px){.header__toggle{margin:0;font-size:12px;font-weight:0}}

.border_country{cursor:pointer;margin-bottom:10px;margin-right:10px}.back{margin-bottom:60px}.btn{font-size:20px;box-shadow:0 0.4rem 0.8rem 0 rgba(0,0,0,0.2);width:-webkit-max-content;width:-moz-max-content;width:max-content;padding:10px 25px;cursor:pointer;background-color:#fff;border-radius:5px}.btn i{margin-right:10px}@media (max-width: 530px){.btn{font-size:14px;padding:8px 23px}}.container-summary{width:100%;padding:70px}@media (max-width: 530px){.container-summary{font-size:10px;padding:0}}@media (max-width: 530px){.container-summary .back{margin-left:20px}}.summary{display:flex;justify-content:space-between;font-size:20px;flex-wrap:wrap;overflow:hidden}@media (max-width: 530px){.summary{justify-content:center;width:100%}}.summary_flag{margin-bottom:30px}.summary_flag img{width:90vw;overflow:hidden}@media (min-width: 768px){.summary_flag img{max-width:560px;max-height:400px}}@media (max-width: 530px){.summary_flag img{width:90vw}}.summary_details{flex:0.8 1}@media (max-width: 530px){.summary_details{font-size:16px;width:100%}}.summary_details-header{font-size:25px;font-weight:bold;margin-bottom:20px}.summary_details-body{display:flex;justify-content:space-between;flex-wrap:wrap}@media (max-width: 530px){.summary_details-body--1{margin-bottom:20px}}.summary_details-footer{margin-top:50px;display:flex;justify-content:flex-start;align-items:flex-start;flex-wrap:wrap}@media (max-width: 530px){.summary_details-footer{flex-direction:column}}.summary_details-footer b{margin-right:10px;margin-bottom:20px}

